.-col-group-container {
  border-bottom: 1px solid #ddd;
}

.-col-group {
  display: inline-block;
  margin-bottom: 5px;
  padding: 0;
  width: 150px;
  vertical-align: top;
}
.-col-group-title {
  display: block;
  margin: 5px;
  padding: 5px;
  border-bottom: 1px solid #ddd;
  font-size: 18px;
}
.-col-group > li {
  margin-bottom: 5px;
  padding-left: 10px;
  list-style: none;
  line-height: 20px;
  font-size: 12px;
}
.-col-group > li > * {
  margin: 0;
  vertical-align: middle;
}

.-complex-table {
  position: relative;
}
.-complex-table table {
  background: #fff;
}
.-table-header, .-table-body, .-table-footer {
  position: relative;
  overflow: scroll;
}
.-fixed-table {
  position: absolute;
  top: 0;
}
.-fixed-table table {
  width: auto;
}
.-left-fixed {
  -webkit-box-shadow: 1px 0 5px #ddd;
          box-shadow: 1px 0 5px #ddd;
}
.-right-fixed {
  -webkit-box-shadow: 1px 0 5px #ddd;
          box-shadow: 1px 0 5px #ddd;
}

.-summary-row {
  font-weight: bold;
  background-color: #eee !important;
}

.-page-size-select {
  display: inline-block;
  width: 65px;
}
